.loading {
  z-index: 99;
  background: #000;
  place-items: center;
  width: 100%;
  height: 100vh;
  font-family: Orbitron, sans-serif;
  display: none;
  position: fixed;
  inset: 0;
}

.loading__box {
  border: 3px solid #6cff8c7a;
  border-color: #6cff8c7a #6cff8d;
  width: 500px;
  height: 200px;
  position: relative;
}

@media (width <= 600px) {
  .loading__box {
    width: 90vw;
  }
}

.loading__bar {
  background: #ccc;
  border-radius: 2px;
  width: 90%;
  height: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.loading__bar--inner {
  background: #6cff8d;
  border-radius: 2px;
  width: 0;
  height: 100%;
}

.loading__text {
  color: #fff;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  position: relative;
}

@media (width <= 355px) {
  .loading__text {
    font-size: 17px;
  }
}

.loading__text--dot {
  background: #fff;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: 0 3px;
  animation: 1s infinite pulse;
}

@keyframes pulse {
  from {
    opacity: 0;
    background: #6cff8d;
  }

  to {
    opacity: 1;
  }
}

.loading__text--border {
  background: #6cff8c7a;
  width: 85%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.loading__counter {
  color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 70%;
  left: 0;
}

.loading__counter--number {
  color: #6cff8d;
}

.loading__width_animation {
  animation: 1s forwards width_animation;
}

@keyframes width_animation {
  0% {
    width: 85%;
  }

  100% {
    width: 250px;
  }
}

.loading__svg {
  opacity: 0;
  width: 85%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.landing {
  color: #fff;
  background: #111 0 0 / cover no-repeat;
  width: 100%;
  height: 100vh;
  font-family: Forum, sans-serif;
  font-style: normal;
  font-weight: 300;
  position: relative;
  overflow: hidden;
}

.landing .header {
  z-index: 2;
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
}

.landing .header__wrap {
  justify-content: space-between;
  align-items: center;
  width: 60vw;
  padding: 10px;
  display: flex;
}

.landing .header__left {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.landing .header__left li {
  font-size: 18px;
  font-weight: 700;
}

@media (width <= 1200px) {
  .landing .header__wrap {
    width: 100vw;
  }
}

@media (width <= 650px) {
  .landing .header__sm {
    display: none;
  }
}

@media (width <= 370px) {
  .landing .header {
    left: 36%;
  }

  .landing .header__wrap {
    gap: 20%;
  }

  .landing .header .coolLink[data-text="Home"] {
    display: none;
  }
}

.landing .socials {
  z-index: 2;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: absolute;
  bottom: 10rem;
  left: 50%;
  transform: translateX(-50%);
}

.landing .scrollDown {
  z-index: 1;
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
}

.landing .scrollDown__wheel {
  border: 2px solid #fff;
  border-radius: 10px;
  width: 24px;
  height: 42px;
}

.landing .scrollDown__wheel--inner {
  background: #fff;
  border-radius: 50%;
  width: 4px;
  height: 4px;
  margin-top: 5px;
  animation: .5s linear infinite movingwheel;
  position: absolute;
  left: 10px;
}

@keyframes movingwheel {
  0% {
    top: 1px;
  }

  25% {
    top: 2px;
  }

  50% {
    top: 3px;
  }

  75% {
    top: 2px;
  }

  100% {
    top: 1px;
  }
}

.landing .scrollDown .scrollDown__arrows {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.landing .scrollDown .scrollDown__arrows .scrollDown__arrow {
  border-bottom: 2px solid #fff;
  border-right: 2px solid #fff;
  width: 15px;
  height: 15px;
  animation: 1s infinite alternate scrollAnim;
  transform: rotate(45deg);
}

.landing .scrollDown .scrollDown__arrows .scrollDown__arrow:first-child {
  animation-delay: .1s;
}

.landing .scrollDown .scrollDown__arrows .scrollDown__arrow:nth-child(2) {
  animation-delay: .2s;
}

.landing .scrollDown .scrollDown__arrows .scrollDown__arrow:nth-child(3) {
  animation-delay: .3s;
}

@keyframes scrollAnim {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
  }
}

.intro {
  text-align: center;
  z-index: 2;
  width: 80%;
  font-family: Forum, sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.intro h4, .intro h1 {
  animation: 2s infinite flip;
  animation-delay: calc(.2s * var(--i));
  margin: 0;
  font-family: Forum, sans-serif;
  font-size: 40px;
  font-style: normal;
  font-weight: bold;
}

.intro h4 {
  text-transform: uppercase;
  margin-bottom: 10px;
  font-size: 25px;
}

.intro h1 {
  text-transform: uppercase;
}

.intro h3 {
  color: #0000;
  text-transform: uppercase;
  background-image: url("https://media.giphy.com/media/26BROrSHlmyzzHf3i/giphy.gif");
  background-size: cover;
  -webkit-background-clip: text;
  background-clip: text;
}

@keyframes move {
  0%, 100% {
    background-position: 200% 0;
  }

  50% {
    background-position: 0 200%;
  }
}

@keyframes glitch {
  2%, 64% {
    transform: translate(2px)skew(0deg);
  }

  4%, 60% {
    transform: translate(-2px)skew(0deg);
  }

  62% {
    transform: translate(0)skew(5deg);
  }
}

.centered_link {
  cursor: pointer;
  font-size: 40px;
  position: absolute;
  top: 40%;
  left: 10%;
}

.coolLink {
  place-items: center;
  display: grid;
  position: relative;
  overflow: hidden;
}

.coolLink:before {
  content: "";
  transform-origin: 100%;
  background: currentColor;
  width: 100%;
  height: 2px;
  transition: transform .4s cubic-bezier(.5, .5, .3, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale3d(0, 1, 1);
}

.coolLink:after {
  content: attr(data-text);
  height: 100%;
  transition: transform .3s cubic-bezier(.5, .5, .3, 1);
  position: absolute;
  top: 0;
  transform: translate3d(150%, 0, 0);
}

.coolLink span {
  transition: transform .3s cubic-bezier(.5, .5, .3, 1);
}

.coolLink:hover:before {
  transform-origin: 0;
  transform: scale3d(1, 1, 1);
}

.coolLink:hover:after {
  transform: translate3d(0, 0, 0);
}

.coolLink:hover span {
  transform: translate3d(-150%, 0, 0);
}

.coolButton {
  cursor: pointer;
  color: #fff;
  background: none;
  border: none;
  width: 175px;
  height: 120px;
  animation: 2s linear infinite wiggle;
  position: relative;
}

.coolButton span {
  font-family: Forum, sans-serif;
}

.coolButton:before, .coolButton:after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.coolButton:before {
  content: "";
  clip-path: path("M154.5,88.5 C131,113.5 62.5,110 30,89.5 C-2.5,69 -3.5,42 4.5,25.5 C12.5,9 33.5,-6 85,3.5 C136.5,13 178,63.5 154.5,88.5 Z");
  background: #fff;
  transition: -webkit-clip-path .5s cubic-bezier(.585, 2.5, .645, .55), clip-path .5s cubic-bezier(.585, 2.5, .645, .55);
}

.coolButton:hover:before {
  clip-path: path("M143,77 C117,96 74,100.5 45.5,91.5 C17,82.5 -10.5,57 5.5,31.5 C21.5,6 79,-5.5 130.5,4 C182,13.5 169,58 143,77 Z");
}

.coolButton span {
  mix-blend-mode: difference;
  font-weight: 500;
}

.coolButton:after {
  content: "";
  transition: transform .5s cubic-beizer(.585, 2.5, .645, .55);
  border: 1px solid #fff;
  border-radius: 58% 42% 55% 45% / 56% 45% 55% 44%;
  width: 95%;
  height: 85%;
  top: 6px;
  transform: rotate(-20deg);
}

.coolButton:hover:after {
  transform: translate3d(0, -5px, 0);
}

@media (width <= 800px) {
  .coolButton {
    font-size: 23px;
    transform: scale3d(.7, .7, .7);
  }
}

@keyframes wiggle {
  0%, 7% {
    transform: rotateZ(0);
  }

  15% {
    transform: rotateZ(-15deg);
  }

  20% {
    transform: rotateZ(10deg);
  }

  25% {
    transform: rotateZ(-10deg);
  }

  30% {
    transform: rotateZ(6deg);
  }

  35% {
    transform: rotateZ(-4deg);
  }

  40%, 100% {
    transform: rotateZ(0);
  }
}

.coloredIcon {
  color: #fff;
  font-size: 35px;
}

.coloredIcon:hover {
  animation: .2s infinite coloredIconAnimation;
}

@keyframes coloredIconAnimation {
  0% {
    text-shadow: 5px 4px #f44336, -5px -6px #2196f3;
  }

  25% {
    text-shadow: -5px -6px #f44336, 5px 4px #2196f3;
  }

  50% {
    text-shadow: 5px -4px #f44336, -8px 4px #2196f3;
  }

  75% {
    text-shadow: -8px -4px #f44336, -5px -4px #2196f3;
  }

  100% {
    text-shadow: -5px 0 #f44336, 5px -4px #2196f3;
  }
}

.coolCircleEyeButton {
  display: inline-block;
  position: relative;
}

.coolCircleEyeButton .textcircle {
  width: 250px;
  animation: 7s linear infinite rotate;
  display: block;
}

.coolCircleEyeButton .textcircle text {
  text-transform: uppercase;
  fill: #000;
  font-size: 32px;
}

@keyframes rotate {
  to {
    transform: rotate(-360deg);
  }
}

.coolCircleEyeButton:hover text {
  font-weight: 500;
}

.coolCircleEyeButton .eye__inner, .coolCircleEyeButton .eye_iris, .coolCircleEyeButton .eye__lashes-up {
  animation: 2s step-end infinite hide;
}

@keyframes hide {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }
}

.coolCircleEyeButton .eye__lashes-down {
  animation: 2s step-end infinite show;
}

@keyframes show {
  0% {
    opacity: 1;
  }

  10% {
    opacity: 0;
  }
}

.eye {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.eye__outer, .eye__inner, .eye__lashes-up, .eye__lashes-down {
  stroke: #000;
  fill: none;
  stroke-width: 1.5px;
}

.eye__lashes-down {
  opacity: 0;
}

.project_tags {
  flex-direction: column;
  gap: 1rem;
  display: flex;
  position: absolute;
  bottom: 1rem;
}

.coolFunnyLink {
  font-family: Forum, sans-serif;
  position: relative;
}

.coolFunnyLink svg {
  stroke-width: 1px;
  width: 95px;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.coolFunnyLink svg path {
  stroke-dasharray: 1;
  stroke-dashoffset: 1px;
  transition: stroke-dashoffset .6s cubic-bezier(.7, 0, .3, 1);
}

.coolFunnyLink:hover svg path {
  stroke-dashoffset: 0;
  transition-timing-function: cubic-bezier(.8, 1, .7, 1);
}

.description {
  font-family: Forum, sans-serif;
}

